



































































































.footer-view{
        background-color: #000;
        margin-top: 115px;
        position: relative;
    .inner{
        width: 78.6%;
        margin: 0 auto;
        padding-top: 126px;
    }
    @media screen and (max-width: 991px){
        .inner{
            padding-top: 60px;
            
        }
    } 
}
.toTop{
    position: absolute;
    height: 75px;
    width: 75px;
    border-radius: 1px;
    margin-left: auto;
    border: 1px solid rgba(255,255,255,0.4);
    bottom: 35px;
    right: 36px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease .35s;
    &::before{
        content: "";
        display: inline-block;
        border-top: solid 3px rgba(255,255,255,0.8);
        border-right: solid 3px rgba(255,255,255,0.8);
        width: 10px;
        height: 10px;
        transition: all ease .35s;
        transform: rotate(-45deg);
        z-index: 1;
        margin-top: 9px;
    }
}
.footBottom{
    margin-top: 190px;
    padding-bottom: 34px;
    color: #fff;
    position: relative;
    .copy{
        font-size: 1rem;
        opacity: 0.5;
    }
}
.footMail{
    height: 3.5rem;
    width: 15.5625rem;
    margin-top: 2.6875rem;
    button{
        width: 100%;
        font-size: 1.125rem;
        font-weight: bold;
        padding: 18px;
        color: white;      
        background: transparent;
        i{
            margin-left: 1rem;
        }
        &:active{
            background: rgba(255, 255, 255, 0.3) !important;
        }
    }
    @media screen and (max-width: 991px){
        width: 100%;
    }
}
.footData{        
    .list{
        display:flex;
        flex-flow: row wrap;
        .list-item{
            margin: 0.25rem  .5rem;
        }
        .list-link{
            color: white;
            font-weight: bold;
            font-size: 2rem;
            border: none;
            cursor: pointer;
            outline: 0;
            background: transparent;
            transition: all ease .35s;
            &:hover{
                color: rgba(255,255,255,0.4);
                
            }
        }
    }
    @media screen and (max-width: 991px){
        margin-top: 2rem;
        .list{
            .list-item{
                width: 50%;
                margin: .25rem 0    ;
            }
        }
    }
}
